<template>
	<view class="box">
		<image :src="newsDetail.pictureUrl" mode=""></image>
		<view class="content">
			<u-parse :html="newsDetail.text"></u-parse>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				newsDetail:'',
			}
		},
		methods: {
			
		},
		onLoad(options) {
			console.log(options)
			this.$myRequest({
				url:'/news/getNewsDetails',
				data:{
					newsId:options.id
				}
			}).then(res=>{
				console.log(res)
				this.newsDetail=res.data.object
				uni.setNavigationBarTitle({
					title:res.data.object.title
				})
			}).catch(err=>{
				console.log(err)
			})
		}
	}
</script>

<style>
	.box{
		padding: 0 15rpx;
	}
	.box /deep/ uni-image{
		width: 100%!important;
	}
</style>
